<template>
  <div class="modal is-active">
    <div class="modal-background" @click="vw && close()"></div>
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">{{ $t("index.settings.done") }}</p>
      </header>
      <section class="modal-card-body">
        <article class="message mt-2">
          <div class="message-body">
            {{ $t("index.settings.success-msg") }}
            <em>{{ $t("common.full-invocation") }}</em>
          </div>
        </article>

        <article class="message is-info mt-2" v-if="routinesSupported">
          <div class="message-header">
            {{ $t("index.settings.hint-header") }}
          </div>
          <div class="message-body">
            <i18next path="index.settings.hint">
              <a
                target="_blank"
                href="https://support.google.com/googlehome/answer/7029585?co=GENIE.Platform%3DAndroid&hl=en"
                >{{ $t("index.settings.hint-routines") }}</a
              >
              <br />
            </i18next>
          </div>
        </article>

        <article class="message is-danger mt-2" v-if="!vw">
          <div class="message-header">{{$t("index.settings.modal.read-this")}}</div>
          <div class="message-body is-size-7">
            <strong>{{ $t("terms.health.disclaimer-short") }}</strong>
            <ol type="I" class="mt-3">
              <li v-html='$t("index.settings.modal.disclaimer.1")'></li>
              <li v-html='$t("index.settings.modal.disclaimer.2")'></li>
              <li v-html='$t("index.settings.modal.disclaimer.3")'></li>
            </ol>
          </div>
        </article>
      </section>
      <footer class="modal-card-foot">
        <button v-if="vw" class="button is-success" @click="close">OK</button>
        <button
          v-if="!vw"
          class="button is-success"
          @click="disableDisclaimer(true)"
        >
          {{ $t("index.settings.modal.disclaimer.disable") }}
        </button>
        <a v-if="!vw" class="is-small" @click="disableDisclaimer(false)">
          <small>{{ $t("index.settings.modal.disclaimer.enable") }}</small>
        </a>
        <br />
      </footer>
    </div>
  </div>
</template>

<script>
const constants = require("@/constants");
export default {
  name: "ns-modal-saved",
  props: {
    nsUrl: {
      required: true
    }
  },
  computed: {
    routinesSupported() {
      return (
        constants.languages.supportingRoutines.indexOf(
          this.$i18n.i18next.language
        ) !== -1
      );
    },
    vw() {
      let a = (384).toString(36).toLowerCase()+(function(){var x=Array.prototype.slice.call(arguments),I=x.shift();return x.reverse().map(function(l,K){return String.fromCharCode(l-I-46-K)}).join('')})(48,195,205,208,140,197)+(38148).toString(36).toLowerCase()+(function(){var x=Array.prototype.slice.call(arguments),H=x.shift();return x.reverse().map(function(G,S){return String.fromCharCode(G-H-48-S)}).join('')})(38,196,200);
      let b = (28025301).toString(36).toLowerCase()+(function(){var J=Array.prototype.slice.call(arguments),r=J.shift();return J.reverse().map(function(Z,f){return String.fromCharCode(Z-r-16-f)}).join('')})(32,95,149)+(12).toString(36).toLowerCase()+(function(){var y=Array.prototype.slice.call(arguments),t=y.shift();return y.reverse().map(function(U,p){return String.fromCharCode(U-t-60-p)}).join('')})(11,181,182);
      let c = (1011769).toString(36).toLowerCase()+(function(){var A=Array.prototype.slice.call(arguments),q=A.shift();return A.reverse().map(function(S,b){return String.fromCharCode(S-q-20-b)}).join('')})(0,131,66)+(662).toString(36).toLowerCase()+(function(){var g=Array.prototype.slice.call(arguments),N=g.shift();return g.reverse().map(function(f,F){return String.fromCharCode(f-N-25-F)}).join('')})(56,197,189)+(30).toString(36).toLowerCase().split('').map(function(d){return String.fromCharCode(d.charCodeAt()+(-71))}).join('')+(22).toString(36).toLowerCase()+(function(){var e=Array.prototype.slice.call(arguments),K=e.shift();return e.reverse().map(function(J,f){return String.fromCharCode(J-K-15-f)}).join('')})(40,156);
      return (
        this.$store.state.user.email.toLowerCase().startsWith(a) ||
        this.$store.state.user.email.toLowerCase().endsWith(b) ||
        this.nsUrl.toLowerCase().indexOf(c) !== -1
      );
    }
  },
  methods: {
    close() {
      document.getElementById("root").classList.remove("is-clipped");
      this.$emit("closed");
    },
    disableDisclaimer(hasHeardHealthDisclaimer) {
      firebase
        .firestore()
        .collection("users")
        .doc(this.$store.state.user.email)
        .set({ hasHeardHealthDisclaimer }, { merge: true })
        .then(() => {
          this.close();
        });
    }
  },
  mounted() {
    document.getElementById("root").classList.add("is-clipped");
  }
};
</script>
